.backdrop-about-modal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.team-modal-link {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.about-modal {
  padding-bottom: 10px;
  overflow: hidden;
  position: absolute;
  width: 220px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #282627e4;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 30px;

  @media screen and (min-width: 768px) {
    max-width: initial;
    width: 740px;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}

.about-modal_title {
  margin-bottom: 100px;
}

.logo-creators {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0px;
  left: 0px;
}

.about-modal__btn {
  position: absolute;
  top: 20px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: url(../images/pointer.png), auto;
  margin: 0;
  padding: 0;
  background-color: rgb(185, 191, 185);
  border: none;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    fill: orange;
  }
}

.about-modal__list {
  box-sizing: border-box;
  padding-bottom: 10px;
  flex-flow: wrap;
  display: flex;
  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1280px) {
    flex-flow: nowrap;
  }
}
.about-modal__item {
  display: flex;
  flex-direction: row;
  height: auto;
  overflow: hidden;
  border: 1px solid black;
  border-radius: 30px;
  background-color: gray;
  flex-wrap: wrap;
  display: block;
  @media screen and (min-width: 320px) {
    width: 70px;
  }
  @media screen and (min-width: 768px) {
    width: 123px;
  }
  @media screen and (min-width: 1280px) {
    width: 220px;
  }
}

.about-modal__img {
  height: 200px;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  object-fit: cover;
  display: none;

  @media screen and (min-width: 768px) {
    display: flex;
    height: 160px;
  }
  @media screen and (min-width: 1280px) {
    height: 220px;
  }
}

.about-modal__img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.about-modal__text {
  padding: 7px;
  text-align: center;
}

.about-modal__text--name {
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 5px;
}

.about-modal__text--position {
  height: 28px;
  color: orange;
  font-size: 12px;
  margin-bottom: 5px;
}
